<template>
    <div class="login">
        <div class="login_form">
            <div class="login_form_user login_item">
                <el-input v-model="name" placeholder="请输入账号"></el-input>
            </div>
            <div class="login_form_psw  login_item">
                <el-input v-model="psw" placeholder="请输入密码"></el-input>
            </div>
            <div class="login_form_btn  login_item">
                <el-button type="primary" @click="bindClickLogin">登录</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    
    export default {
        data () {
            return {
                name: '',
                psw: ""
            }
        },
        created () {
           console.log(this.name) 
        },
        methods: {
            bindClickLogin () {
                let that = this;
                let url = '/api'+this.$common.login;
                let data = {
                    id:that.name,
                    psw:that.psw
                }
                that.$http.post(url,data).then((res)=>{
                    console.log(res)
                    if ( res.code == 200 ) {
                        this.$message({
                            message: '登录成功',
                            type: 'success'
                        });
                        sessionStorage.setItem('token',res.token);
                        sessionStorage.setItem('uid',res.uid);
                        that.$store.commit('SET_UID',res.uid);
                        that.$store.commit('SET_TOKEN',res.token);
                        that.$router.push({ path:"/home" })
                    } else {
                        that.$message.error('用户密码不正确');
                    }
                }).catch(data=>{

                })
            }
        },
        mounted () {
           
        },
    }
</script>


<style scoped>
    .login {
        min-height: 100%;
        width: 100%;
        height:100%;
        background-color: #2d3a4b;
        overflow: hidden;
        box-sizing:border-box;
        position: absolute;
        display:flex;
        align-items: center;
        justify-content: center;
    }
    .login_form {
        width:550px;
        padding:100px;
    }
    .login_form_btn button {
        width:100%;
    }
    .login_item {
        padding-bottom: 20px;
    }
</style>



